function Sliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let isLoop = slider.classList.contains("_loop")
        let isNested = slider.classList.contains("_nested")

        SlidersArray[index] = new Swiper(`.slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            loop: isLoop,
            nested: isNested,
            navigation: {
                nextEl: `.slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
            scrollbar: {
                el: `.slider[data-slider-id="${index}"] .swiper-scrollbar`,
                draggable: true,
                snapOnRelease: true,
                dragSize: 'auto',
            },
        })
    })
}

function loyaltySlider() {
    var swiper = new Swiper('.loyalty-advantages .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        slideToClickedSlide: true
    })
}

function appSlider() {
    var swiper = new Swiper('.app-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        direction: "vertical",
        freeMode: true,
        scrollbar: {
            el: `.app-slider .swiper-scrollbar`,
            draggable: true,
            snapOnRelease: true,
            dragSize: 'auto',
        },
    })
}

function calendarSliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".calendar-slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)

        SlidersArray[index] = new Swiper(`.calendar-slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: 1,
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            direction: 'vertical'
        })
    })
}


Sliders()
appSlider()
calendarSliders()
loyaltySlider()
