let rangeBlocks = document.querySelectorAll(".range-field")

rangeBlocks.forEach(rangeBlock => {
    let rangeSlider = rangeBlock.querySelector(".range-slider")
    let rangeOutput = rangeBlock.querySelector(".range-field__val")
    let min = +rangeBlock.getAttribute("data-min")
    let max = +rangeBlock.getAttribute("data-max")

    if (rangeSlider) {
        noUiSlider.create(rangeSlider, {
            start: min,
            connect: [true, false],
            step: 1,
            range: {
                'min': min,
                'max': max
            }
        });

        rangeSlider.noUiSlider.on('update', function (values, handle) {
            rangeOutput.innerHTML = Math.floor(values[handle]);
        });

    }
})

$(".manufacture-prop__show").click(function () {
    $(this).parents(".manufacture-prop").toggleClass("_active")
    $(this).siblings(".manufacture-prop__hidden").slideToggle()
})

$('[name="when"]').change(function () {
    let val = $(this).attr("data-value")
    if(val === "future"){
        $(".quiz-calendar").addClass("_active")
        $(".mob-calendar").addClass("_active")
    }else{
        $(".quiz-calendar").removeClass("_active")
        $(".mob-calendar").removeClass("_active")
    }
})

$('[name="part"]').change(function () {
    let checked = $(this).prop("checked")
    if(checked){
        $(".order-part").addClass("_active")
    } else{
        $(".order-part").removeClass("_active")
    }
})

$(".hour-field").each(function (i,el) {
    let plus = $(el).find("._plus")
    let minus = $(el).find("._minus")
    let val = $(el).find("._val")

    $(plus).click(function () {
        $(val).html(+$(val).html()+1)
    })
    $(minus).click(function () {
        if(+$(val).html() > 3){
            $(val).html(+$(val).html()-1)
        }
    })
})

let quizSteps = $(".quiz-step")
renderQuizProgressbar()

function renderQuizProgressbar() {
    let progress = getActiveStepNum() / quizSteps.length * 100
    console.log(progress)
    $(".quiz-progress__bar span").css("width",`${progress}%`)
    $(".quiz-progress ._current").html(getActiveStepNum())
}

function getActiveStepNum() {
    return +$(".quiz-step._active").attr("data-step")
}

function changeStep(num) {
    $(".quiz-step").removeClass("_active")
    $(`.quiz-step[data-step="${num}"]`).addClass("_active")
    renderQuizProgressbar()
}

$(".quiz-nav ._next").click(function () {
    if(getActiveStepNum() === 8){
        $(".quiz").addClass("_last")
    } else{
        $(".quiz").removeClass("_last")
    }

    $(".quiz-nav ._prev").removeClass("_hide")
    changeStep(getActiveStepNum() + 1)

    if(getActiveStepNum() === 9){
        $(".quiz-nav ._next").addClass("_hide")
    }
})

$(".quiz-nav ._prev").click(function () {
    if(getActiveStepNum() === 9){
        $(".quiz").addClass("_last")
    } else{
        $(".quiz").removeClass("_last")
    }

    if(getActiveStepNum() > 1){
        changeStep(getActiveStepNum() - 1)
    }

    $(".quiz-nav ._next").removeClass("_hide")

    if(getActiveStepNum() === 1){
        $(".quiz-nav ._prev").addClass("_hide")
    }
})

$(".quiz ._finish").click(function () {
    $(".quiz").addClass("_hide")
})

$(".quiz-thank ._restart").click(function () {
    changeStep(1)
    $(".quiz-nav ._prev").addClass("_hide")
    $(".quiz-nav ._next").removeClass("_hide")
    $(".quiz").removeClass("_hide")
})
