function partnersSlider() {
    let swiper = new Swiper('.partners-slider__container', {
        wrapperClass: "partners-slider__wrapper",
        slideClass: "partners-slider__slide",
        slidesPerView: "auto",
        spaceBetween: 0,
    })

    $(".partners-tabs__item").click(function () {
        let tab = +$(this).attr("data-tab")
        $(".partners-tabs__item").removeClass("_active")
        $(this).addClass("_active")
        swiper.slideTo(tab)
    })

    swiper.on('slideChange', function () {
        $(".partners-tabs__item").removeClass("_active")
        $(`.partners-tabs__item[data-tab="${swiper.activeIndex}"]`).addClass("_active")
    });
}
partnersSlider()

$('[name="driverType"]').change(function () {
    let val = $(this).val()
    $(`.driver-type`).removeClass("_active")
    $(`.driver-type.${val}`).addClass("_active")
})
